@import 'mixins';
@import 'variables';

.index_title {
    font-size: 20px;
    padding: 10px 0;
    border-bottom: 2px solid #ffad00;
    font-weight: 700;
    margin-bottom: 20px;
}

.form_box {
    position: absolute;
    top: 10px;
    right: 0;

    [class^="col-"] {
        padding-right: 0;
        padding-left: 10px;
    }
    .btn {
        width: 100%;
        padding: 6px;
    }
    i span {
        padding-left: 5px;
    }
}

label {
    font-size: 16px;
    text-align: left !important;
    font-weight: 500;
    padding-bottom: 5px;
}

.table > caption + thead > tr:first-child > th, .table > caption + thead > tr:first-child > td, .table > colgroup + thead > tr:first-child > th, .table > colgroup + thead > tr:first-child > td, .table > thead:first-child > tr:first-child > th, .table > thead:first-child > tr:first-child > td {
    border-top: 1px solid #acacac;
}

table th, table td {
    font-size: 15px;
    border: 1px solid #acacac;
}

table th {
    text-align: center;
    background: #ebebeb;
}

table tbody td {
    text-align: right;
}

table caption {
    position: absolute;
    left: -9999px;
    font-size: 0;
    height: 0;
    width: 0;
    overflow: hidden;
}

.row.statistics {
    text-align: right;
    padding-bottom: 10px;

    div p:first-child {
      text-align: left;
    }

    div p+p {
      font-size: 20px;
    }
    div:first-child,
    div:nth-child(2) {
      padding-right: 5px;
    }
    div:nth-child(3) {
      padding-left: 5px;
    }
    div:nth-child(2) {
      padding: 0 5px;
    }
    div:first-child p + p {
        color: #555555;

        font-weight: 800;
        padding: 2px 10px;
        border: 2px solid #448ccb;
    }
    div:nth-child(2) p + p {
        color: #555555;
        font-weight: 800;
        padding: 2px 10px;
        border: 2px solid #00a99d;
    }
    div:nth-child(3) p + p {
        color: #555555;
        font-weight: 800;
        padding: 2px 10px;
        border: 2px solid #8dc63f;
    }
}
.form_custom {
  padding-bottom: 60px;

  &>div:first-child {
    padding-right: 25px;
    .form-group {
      margin-bottom: 25px;
    }
    .control-label {
      padding-left: 5px;
    }
    .btn_dark {
      padding: 12px 25px;
    }

    .form-group:last-child {
      padding-top: 5px;
      margin-bottom: 0;
    }
  }
  &>div:last-child {
    padding-left: 25px;
  }
}


/**  Bootstrap carousel **/
.carousel-inner {
    width: 92%;
    margin: 0 auto;
    .item {
      height: 300px;
    }
}

.carousel-control {
    text-shadow: none;
    &:hover, &:focus {
        .fa-chevron-left,
        .fa-chevron-right {
            color: $brand-secondary;
        }
    }
    &.left, &.right {
        width: 4%;
        background-image: none;
    }
    .fa-chevron-left,
    .fa-chevron-right {
        position: absolute;
        top: 47%;
        z-index: 5;
        display: inline-block;
        color: #000;
    }
    .fa-chevron-left {
        left: 0;
    }
    .fa-chevron-right {
        right: 0;
    }
}

